body {
  background-color: rgb(46, 46, 71);
  color: white;
  font-family: "Coustard", serif;
}

.select-button {
  left: 20px;
  position: relative;
  width: 400px;
  margin-top: 30px;
  margin: 0 auto;
  font-family: "Open Sans", "Helvetica Neue", "Segoe UI", "Calibri", "Arial",
    sans-serif;
  font-size: 18px;
  color: #60666d;
}

.select-label {
  position: relative;
  left: 20px;
  margin-bottom: 40px;
}

.button {
  position: relative;
  width: 200px;

  margin: 5px;
  font-family: "Open Sans", "Helvetica Neue", "Segoe UI", "Calibri", "Arial",
    sans-serif;
  font-size: 18px;
  border-radius: 12px;
}

.form-column {
  width: 600px;
}

.radio-input {
  display: inline-block;
  vertical-align: top;
}

.radio-label {
  display: inline-block;
  vertical-align: top;
  margin-right: 3%;
}
